import React from 'react';
import Typewriter from 'typewriter-effect';
import ImageAvatars from '../Avatar/Avatar';
import DownloadResume from '../Download_Resume/DownloadResume';
import styles from './AnimatedSummary.module.css'

export const AnimatedSummary = ({home}) => {
    return (
        <div className={styles.wrappertext}>
            <div className={styles.imageWrapper}>
            <ImageAvatars/>
            <DownloadResume/>
            </div>
            <div className={styles.wrappedtext}>
            <Typewriter 
  onInit={(typewriter) => {
    typewriter.typeString(`I'm Kamal Gupta MERN Stack Developer`)
      .callFunction(() => {
        console.log('String typed out!');
      })
      .pauseFor(500)
      .deleteAll()
      .callFunction(() => {
        console.log('All strings were deleted');
      })
      .start();
  }}
/>
        </div>




          


        </div>
    )
}
