import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import url_Kamal from './kamal.png'


// const url_Kamal ="https://avatars.githubusercontent.com/u/63225641?s=400&u=5b38cb0458644a53fe8b09da17868d4e12253291&v=4"
const useStyles = makeStyles((theme) => ({
  root: {
      width:'50%',
     
    display: 'flex',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
 
  large: {
    width: "100%",
    
  
  },
}));

export default function ImageAvatars() {
  const classes = useStyles();


  return (
    <div className={classes.root}>

        <img src={url_Kamal} className={classes.large}/>
        
        
      {/* <Avatar alt="Remy Sharp" src={url_Kamal} className={classes.large} /> */}
     
    </div>
  );
}