import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
// import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import {NavLink } from 'react-router-dom'


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    // marginBottom: '64px'
  },
  menuButton: {
    marginRight: theme.spacing(1),
  },
  title: {
       display: 'flex',
    // flexGrow: 1,
    marginLeft: '55%',
    // marginRight: '10%',
    gap:'30%'
//    '&:*':{
  
//    }
    
    
  },
  appbar:{
   
      backgroundColor: '#070707',
        color: '#c4c4c4',
        
           

  },
  links:{
      color:'#c4c4c4',
      textDecoration: 'none',
    //   '&:focus':{
    //     color: '#f0860c',
    //   },
    //   '&:active':{
    //     color: '#f0860c',
    //   }
     
  },
 
 
    

}));

const activeLinks={
    color: '#f0860c'
}

export default function ButtonAppBar({home}) {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <AppBar position="fixed" className={classes.appbar}>
        <Toolbar>
        {!home?<IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon />
          </IconButton>
            :
          <Typography variant="h6" className={classes.title}>

            <NavLink to="/home" className={classes.links} activeStyle={activeLinks}>Home</NavLink >
            <NavLink to="/about"   className={classes.links} activeStyle={activeLinks} >About</NavLink >
           <NavLink to="/project"  className={classes.links} activeStyle={activeLinks}>Project</NavLink >
            <NavLink to="/contact"   className={classes.links} activeStyle={activeLinks}>Contact</NavLink >
    </Typography>
    
    }
          
         
   
         
        
        </Toolbar>
      </AppBar>
    </div>
  );
}
